import { useState } from 'react';

import workoutService from '../services/workouts'

import Info from './Info'
import Movement from './Movement';
import Cardio from './Cardio';

const Template = () => {
const [fullWorkout, setFullWorkout] = useState([])
const [newTheme, setNewTheme] = useState('')
const [newDate, setNewDate] = useState('')
const [newDuration, setNewDuration] = useState('')

const [movement, setMovement] = useState([])
const [newName, setNewName] = useState('')
const [newSets, setNewSets] = useState('')
const [newReps, setNewReps] = useState('')
const [repWeight, setRepWeight] = useState('')

const [cardio, setCardio] = useState([])
const [cardioType, setCardioType] = useState('')
const [cardioDuration, setCardioDuration] = useState('')

const addMovement = (event) =>{
    event.preventDefault()
    const movementId = movement.length >  0
    ? Math.max(...movement.map(m => m.id))
    :0
    
    let reps = (repWeight.split(";").map(Number))
    
    const movementObject = {
        id: movementId +1,
        name: newName,
        weights: reps,
        sets: Number(newSets),
        reps: Number(newReps),
    }
    

    setMovement(movement.concat(movementObject))
    setNewName('')
    setNewSets('')
    setNewReps('')
    setRepWeight('')
    reps=0
    
}

const addCardio = (event) => {
    const cardioId = cardio.length >  0
    ? Math.max(...cardio.map(c => c.id))
    :0

    event.preventDefault()
    const cardioObject = {
        id: cardioId + 1,
        name: cardioType,
        duration: cardioDuration
    }
    setCardio(cardio.concat(cardioObject))
    setCardioType('')
    setCardioDuration('')
}

const addFullWorkout = (event) => {
    event.preventDefault()
    const fullWorkoutObject = {
        ...fullWorkout,
        theme: newTheme,
        date: newDate,
        duration: newDuration,
        movements: movement,
        cardio: cardio,
    }

    workoutService
        .create(fullWorkoutObject)
        .then(returnedWorkout => {
            setFullWorkout(fullWorkout.concat(returnedWorkout))
            setNewTheme('')
            setNewDate('')
            setNewDuration('')
        })
}

const check = (event) => {
    console.log(fullWorkout)
}


    return (
        <section className="addWorkout">
            <article className="addarticle">
                <Info 
                newTheme={newTheme} 
                setNewTheme={setNewTheme} 
                newDate={newDate} 
                setNewDate={setNewDate} 
                newDuration={newDuration} 
                setNewDuration={setNewDuration}
                />
                <Movement
                newName={newName}
                setNewName={setNewName}
                newSets={newSets}
                setNewSets={setNewSets}
                newReps={newReps}
                setNewReps={setNewReps}
                repWeight={repWeight}
                setRepWeight={setRepWeight}
                addMovement={addMovement}
                />
                <Cardio
                cardioType={cardioType}
                setCardioType={setCardioType}
                cardioDuration={cardioDuration}
                setCardioDuration={setCardioDuration}
                addCardio={addCardio}
                />
                <div>
                </div>
            </article>
            {movement.length > 0 && (
            <article className='beingaddedarticle'>
                <h2>Movements being added</h2>
                    {movement.map((item, indx) =>
                        <ul key={indx}>
                        <li>{item.name}</li>
                        <li>{item.sets}x{item.reps}</li>
                        <li>{`${item.weights}kg `}</li>
                        </ul>
                    )}
            {cardio.length > 0 && (
                <div>
                <h2>Cardio being added</h2>
                    {cardio.map((item, indx) => 
                        <ul key={indx}>
                        <li>{item.name}</li>
                        <li>{item.duration}</li>
                        </ul>
                    )}
                </div>
            )}
                    <button onClick={addFullWorkout}>Save this workout</button>
                    <button onClick={check}>check</button>
            </article>
            )}
        </section>
    )

}

export default Template