import React, { useState, useEffect } from 'react'
import {
    Link, useParams
  } from "react-router-dom"
  import workoutService from '../services/workouts'

const Singleworkout = () => {
    const {id} = useParams()
    const [isLoading, setLoading] = useState(true);
    const [singleWorkout, setSingleWorkout] = useState([])
    

    useEffect(()=>{
        workoutService
            .getOne(id)
            .then(oneWorkout => {
                console.log(oneWorkout.data)
                setSingleWorkout(oneWorkout)
                setLoading(false)
            })
    },[])

    if (isLoading) {
        return <div className="App">Loading...</div>;
      }

    return(
        singleWorkout && (
        <section className="singleWorkout"> 
            <article>
                <div className="themeDate">
                    <h2>{singleWorkout.date.split('T')[0]}</h2>
                    <h2>{singleWorkout.theme}</h2>
                    <h2>{singleWorkout.duration}</h2>
                </div>
                        <h4 id="movementsHead">Movements</h4>
                        {singleWorkout.movements.map(info =>
                            <ul className="movementsUl" key={info.id}>
                            <li id="movementName">{info.name}</li>
                            <li id="movementSet">{`${info.sets}x${info.reps}`}</li>
                    
                            {info.weights.map((weight, indx) => 
                                <ul key={indx} className="weightsUl">
                                <li>{indx+1}</li>
                                <li>{`${weight}kg`}</li>
                                </ul>
                                )}
                            </ul>
                         )}
                        <h4 id="cardioHead">Cardio</h4>
                        {singleWorkout.cardio && singleWorkout.cardio.map(cardio => 
                            <ul className="cardioUl" key={cardio.id}>
                            <li>{cardio.name}</li>
                            <li>{cardio.duration}</li>
                            </ul>
                            )}
            </article>
        </section>
        )
    )
}

export default Singleworkout